.container {
    position: relative;
    margin-right: auto;
    margin-left: auto;

    @include media(xxl) {
        width: 1336px; }

    @include media(l-xxl) {
        width: 100%;
        padding-left: 8.204%;
        padding-right: 8.204%; }

    @include media(m-l) {
        width: 708px; }

    @include media(s-m) {
        width: 522px; }

    @include media(xs-s) {
        width: 100%;
        padding-left: 15px;
        padding-right: 15px; } }

.layout-section {
    &:not(:empty) {
        padding-top: 36px;
        padding-bottom: 36px; }

    &:empty {
        display: none;
        margin: 0; } }

.xlblock {
    &:empty {
        display: none; }

    &:not(:empty) {
        margin-top: 48px; } }

.block {
    &:empty {
        display: none; }

    &:not(:empty) {
        margin-top: 36px; } }

.subblock {
    &:empty {
        display: none; }

    &:not(:empty) {
        margin-top: 24px; } }

.xs-hidden {
    @include media(xs-s) {
        display: none; } }

.s-hidden {
    @include media(s-m) {
        display: none; } }
