.section-title-wrap {
    display: flex;
    align-items: center;
    margin-bottom: 24px; }

.section-title {
    line-height: $micro-lh;
    font-family: $bold-f;

    &_h2 {
        @include minMedia(l) {
            font-size: $huge-fz; }

        @include media(m-l) {
            font-size: $bigger-fz; }

        @include maxMedia(m) {
            font-size: $big-fz; } }

    &__more-link {
        margin-top: 3px;
        margin-left: auto; } }

.page-title {
    @include minMedia(l) {
        font-size: $h1-fz; }

    @include media(m-l) {
        font-size: $h2-fz; }

    @include maxMedia(m) {
        font-size: $h3-fz; } }

.page-title-wrap {
    @include minMedia(l) {
        margin-bottom: 36px; }

    @include media(m-l) {
        margin-bottom: 24px; }

    @include media(s-m) {
        margin-bottom: 24px; }

    @include media(xs-s) {
        margin-bottom: 16px; } }
