.site-nav {
    margin: 0;
    padding: 0;
    font-size: 0;

    &__item {
        display: inline-block;
        &:not(:last-child) {
            margin-right: 25px; } }

    &__link {
        display: inline-block;
        font-size: $small-fz;
        color: $light-c;
        text-decoration: none;
        text-transform: uppercase; } }
