footer {
    color: $primary-dark-c;
    margin-top: 36px;

    @include media(xs-s) {
        text-align: center; }

    a {
        text-decoration: none;
        &:hover,
        &:focus {
            text-decoration: underline; } } }

.footer__main {
    padding-bottom: 30px;
    border-bottom: 1px solid $light-gray;

    @include minMedia(s) {
        display: flex;
        align-items: flex-start; }

    @media (min-width: 568px) and (max-width: 1249px) {
        flex-wrap: wrap; } }

.footer-nav {

    &__subcol {

        &:not(:last-child) {
            margin-bottom: 24px; }

        @include media(s-m) {
            width: 50%; } }

    &__col {
        @include minMedia(s) {
            flex-grow: 1; }

        @include minMedia(l) {
            width: calc(100% / 4); }

        @include media(xxl) {
            &:not(:last-child) {
                margin-right: 8px; } }

        @media (min-width: 1250px) and (max-width: 1509px) {
            &:nth-child(1) {
                margin-right: 12px; }

            &:nth-child(2),
            &:nth-child(3) {
                margin-right: 24px; } }

        @include media(m-l) {
            width: calc(100% / 3);

            &:nth-child(2) {
                padding-right: 24px; } }

        @include media(s-m) {
            width: 50%;

            &:not(:last-child()) {
                margin-bottom: 36px; }

            &:nth-child(3),
            &:nth-child(4) {
                width: 100%;
                display: flex;
                flex-grow: 1; } }

        @include media(xs-s) {
            &:not(:last-child()) {
                margin-bottom: 24px; } } }

    &__title {
        margin-bottom: 12px;
        font-family: $bold-f;

        @include minMedia(l) {
            font-size: $bigger-fz; }

        @include maxMedia(l) {
            font-size: $big-fz; } }

    &__list {
        padding: 0;
        margin: 0; }

    &__item {
        &:not(:last-of-type) {
            margin-bottom: 12px; } }

    &__link {
        font-size: $mini-fz;
        line-height: $base-lh;
        color: $primary-dark-c;
        text-decoration: underline;

        &:hover,
        &:focus {
            text-decoration: none; } } }

.footer-info {

    @include media(xxl) {
        padding-left: 56px; }

    @media (min-width: 768px) and (max-width: 1249px) {
        width: 100%;
        display: flex;
        margin-top: 48px; }

    &__contacts {
        @media (min-width: 1250px) {
            margin-bottom: 36px; }

        @media (min-width: 768px) and (max-width: 1249px) {
            width: calc(100% / 3);
            order: 1;
            margin-left: auto; }

        @include media(s-m) {
            order: 1;
            flex-shrink: 0;
            width: 50%;
            margin-left: 24px;
            margin-bottom: 36px; }

        @include media(xs-s) {
            margin-bottom: 36px; } }

    &__payment {} }


.footer__contact-wrap {
    &:first-of-type {
        margin-bottom: 24px; } }

.footer-contact {

    &__phone {
        @include minMedia(l) {
            font-size: $bigger-fz; }

        @include maxMedia(l) {
            font-size: $big-fz; } }

    &__email {
        @include minMedia(l) {
            font-size: $big-fz; }

        @include maxMedia(l) {
            font-size: $medium-fz; } }

    &__phone,
    &__email {
        color: $dark-c;
        font-family: $bold-f;
        &:hover,
        &:focus {
            text-decoration: none; } }

    &__email {
        &:hover,
        &:focus {
            color: $primary-blue; } }

    &__text {
        font-size: $small-fz;
        color: $base-gray; } }

.footer__copyright {
    margin-top: 16px;
    margin-bottom: 16px;
    font-size: $small-fz;

    @include minMedia(s) {
        display: flex;
        align-items: center;
        justify-content: space-between; } }

.footer__perfectura-copyright {
    display: inline-block;
    color: inherit;

    @include media(xs-s) {
        margin-top: 8px; } }

.payment__text {
    color: $base-gray; }

.footer__notice,
.payment__text {
    font-size: $small-fz;
    line-height: $base-lh;
    @include minMedia(m) {
        width: 290px; }

    @include media(xs-s) {
        max-width: 70%;
        margin-left: auto;
        margin-right: auto; } }

.footer__notice {
    margin-top: 30px; }

.footer__payment-methods {
    margin-bottom: 12px; }

.payment-methods {
    font-size: 0;

    &__item {
        line-height: 20px;
        vertical-align: middle;

        &:first-child {
            height: 20px; }

        &:not(:first-child) {
            height: 14px; }

        &:not(:last-child) {
            margin-right: 16px; } } }
